<template>
  <div class="glass-bg box-show" :class="{'user-left-menu':showUserLeftMenu}" style="width:268px; height:100%; top:0; left:-268px; position:fixed; box-sizing:border-box; border-radius:0;">

  </div>
</template>

<script>
export default {
  name: 'user-left-menu',

  props: [
    'showUserLeftMenu'
  ]
}
</script>

<style scoped>
  .user-left-menu {
    left:0!important;
  }
</style>



